import React from 'react';
import { Controller, RenderNodeProps, Block } from '@ali/4ever-cangjie';
import { SimpleLazyLoad } from '@ali/4ever-component';
import { CalendarCardPluginConfig } from '../types';
import { CustomizedBiCardPluginType } from '@ali/4ever-factory';

export default function createRenderNode(
  calendarCardPluginConfig: CalendarCardPluginConfig,
): CustomizedBiCardPluginType['renderNode'] {
  const LazyLoad = calendarCardPluginConfig.lazyload || SimpleLazyLoad;
  const WrappedCalendar = LazyLoad(() => import('../components/Calendar'));
  return (nodeProps: RenderNodeProps<Block>, controller: Controller) => {
    return (
      <WrappedCalendar
        nodeProps={nodeProps}
        controller={controller}
        calendarConfig={calendarCardPluginConfig}
      />
    );
  };
}
